import React from 'react';
import { TeamOutlined, ReadOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import { Menu } from 'antd';
import { useNavigate } from 'react-router-dom';

const DropMenu = () => {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate('/bestseller/PrintBookRaking');
  };

  const items = [
    {
      key: 'categ1',
      label: '',
      icon: <TeamOutlined />,
      children: [
        {
          key: '1',
          label: '',
          onClick: () => navigate('/')
        },
        {
          key: '2',
          label: '',
          onClick: () => navigate('/')
        }
      ],
    },
    {
      key: 'categ2',
      label: ' ',
      icon: <ReadOutlined />,
      children: [
        {
          key: '3',
          label: ' ',
          onClick: handleClick
        },
        {
          key: '4',
          label: ' ',
          onClick: () => navigate('/bestseller/BookStoreRaking')
        }
      ],
    },
    {
      key: 'categ3',
      label: ' ',
      icon: <MailOutlined />,
      onClick: () => navigate('/')
    },
    {
      key: 'categ4',
      label: '',
      icon: <SettingOutlined />,
      children: [
        {
          key: '5',
          label: ' ',
          onClick: () => navigate('/')
        },
        {
          key: '6',
          label: ' ',
          onClick: () => navigate('/')
        },
        {
          key: '7',
          label: ' ',
          onClick: () => navigate('/')
        },
        {
          key: '8',
          label: '  ',
          onClick: () => navigate('/')
        }
      ],
    }
  ];

  return (
    <Menu
      style={{
        position: 'absolute',
        zIndex: 1,
        width: 256,
      }}
      defaultSelectedKeys={['1']}
      defaultOpenKeys={['categ1']}
      mode="inline"
      items={items}
    />
  );
};

export default DropMenu;