import React, { useState, useEffect } from 'react';

const DateTime = () => {
  //   
  const [year, setYear] = useState('');
  const [month, setMonth] = useState('');
  const [week, setWeek] = useState('');

  //  ,  
  useEffect(() => {
    const currentDate = new Date();
    const currentYear = currentDate.getFullYear();
    const currentMonth = currentDate.getMonth() + 1; // getMonth() 0  +1 .

    setYear(currentYear.toString());
    setMonth(currentMonth.toString());
  }, []); //       .

  //   
  const handleWeekChange = (event) => {
    setWeek(event.target.value);
  };

  //    (1990  )
  const yearOptions = [];
  const startYear = 1990;
  const currentYear = new Date().getFullYear();
  for (let year = startYear; year <= currentYear; year++) {
    yearOptions.push(<option key={year} value={year}>{year}</option>);
  }

  //    (1 12)
  const monthOptions = [];
  for (let month = 1; month <= 12; month++) {
    monthOptions.push(<option key={month} value={month}>{month}</option>);
  }

  return (
    <div>
      <h2>
        <select style={{ border: '0', color: "#135784" }} value={year} onChange={(e) => setYear(e.target.value)}>
          {yearOptions}
        </select>

        <select style={{ border: '0', color: "#135784" }} value={month} onChange={(e) => setMonth(e.target.value)}>
          {monthOptions}
        </select>

        <select style={{ border: '0', color: "#135784" }} value={week} onChange={handleWeekChange}>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
        </select>
      </h2>
    </div>
  );
};

export default DateTime;
